.blog-post {
	margin: 80px auto;
	padding: 0 24px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.blog-post-header {
	max-width: 768px;
	margin: 0 0 32px;
	display: flex;
	flex-direction: column;
	align-content: center;
	text-align: start;
}

.blog-post-header__title {
	font-size: 52px;
	font-weight: 800;
	margin: 0 auto 16px;
}

.blog-post-header__subtitle {
	font-size: 20px;
	color: #9BA9B4;
	line-height: 1.5;
	margin: 0 auto 12px;
}

.blog-post-header__container {
	display: flex;
	justify-content: space-between;
}

.blog-post-header__publication-data {
	display: flex;
	align-items: center;
}

.blog-post-header__user-image {
	height: 40px;
	width: 40px;
	border-radius: 50%;
	margin-right: 16px;
}

.blog-post-header__user-name {
	font-weight: 500;
	margin-right: 16px;
}

.blog-post-header__date {
	color: #707D86;
}

.blog-post-header__tags {
	display: flex;
	align-items: center;
}

.blog-post-header__tag {
	border-radius: 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: 8px;
}

.blog-post-header__tag:last-child {
	margin-right: 0;
}

.blog-post-header__tag-title {
	font-size: 12px;
	margin: 6px 12px;
	font-weight: 500;
}

.blog-post-header__tag_theme-product {
	background-color: #5D5DFF;
}

.blog-post-header__tag_theme-engineering {
	background-color: #3B82F6;
}

.blog-post-image {
	width: 100%;
	max-width: 1024px;
	margin: 0 0 32px;
}

.blog-post-article {
	max-width: 768px;
	display: flex;
	flex-direction: column;
	align-content: center;
	text-align: start;
}

.blog-post-article__image-container {
	margin: 0 0 32px;
}

.blog-post-article__image {
	width: 100%;
	margin: 0 0 12px;
}

.blog-post-article__image-label {
	margin: 0;
	font-size: 14px;
	color: #707D86;
	text-align: center;
}

.blog-post-article__text-main {
	color: #9BA9B4;
	font-size: 18px;
	line-height: 1.5;
	margin: 0 0 32px;
}

.blog-post-article__blockquote {
	font-style: italic;
	padding-left: 16px;
	border-left: 2px solid #D9E3EA;
}

.blog-post-article__title {
	margin: 0 0 16px;
}

.blog-post-article__title-large {
	font-size: 32px;
}

.blog-post-article__title-middle {
	font-size: 24px;
}

.blog-post-article__title-small {
	font-size: 18px;
	margin-bottom: 32px;
}

.blog-post-article__links {
	display: flex;
	justify-content: space-between;
	align-items: start;
	margin: 0 0 80px;
}

.blog-post-article__links-text {
	font-style: italic;
	margin: 0 0 16px;
}

.blog-post-article__links-link {
	list-style: none;
	text-decoration: none;
	color: #D9E3EA;
}

.blog-post-article__icons {
	display: flex;
	align-items: center;
	justify-content: center;
	align-self: center;
}

.blog-post-article__link {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 32px;
	height: 32px;
	margin: 0 16px 0 0;
	border-radius: 50%;
}

.blog-post-article__icon {
	width: 100%;
	height: 100%;
	background-position: center;
	background-repeat: no-repeat;
	background-size: contain;
	transition: background-image .25s ease-out;
}

.blog-post-article__icon-twitter {
	background-image: url(../../images/Twitter.svg);
}

.blog-post-article__icon-twitter:hover {
	background-image: url(../../images/Twitter_hover.svg);
}

.blog-post-article__icon-facebook {
	background-image: url(../../images/Facebook.svg);
}

.blog-post-article__icon-facebook:hover {
	background-image: url(../../images/Facebook_hover.svg);
}

.blog-post-article__icon-linkedln {
	background-image: url(../../images/LinkedIn.svg);
}

.blog-post-article__icon-linkedln:hover {
	background-image: url(../../images/LinkedIn_hover.svg);
}

.blogs {
	width: 100%;
	max-width: 1104px;
}

.blogs-header {
	padding-top: 24px;
	padding-bottom: 24px;
	border-top: 1px solid #33363a;
	border-bottom: 1px solid #33363a;
	text-align: start;
	margin: 0 0 40px;
}


@media (max-width: 1023px) {
	.blog-post-image {
		max-width: 768px;
	}
}

@media (max-width: 767px) {
	.blog-post-header {
		text-align: center;
	}

	.blog-post-header__title {
		font-size: 40px;
	}

	.blog-post-header__container {
		flex-direction: column;
		align-items: center;
	}

	.blog-post-header__tags {
		margin-top: 16px;
	}

	.blog-post-article__links {
		flex-direction: column;
		align-items: center;
		text-align: center;
	}
}

@media (max-width: 639px) {
	.blog-post {
		padding: 0 16px;
	}
}